@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700|Source+Sans+Pro:400,600,700&display=swap);


body {
  margin: 0;
  font-family: "Source Sans Pro", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #5cdb95;
  color: #05386b;
}

.app {
  text-align: center;
}

.f-0 {
  font-size: 0;
}

.f-15 {
  font-size: 15px;
}

.rowNumber {
  width: 100px;
}

.sourceWrapper,
.destinationWrapper {
  width: calc(50% - 50px);
  margin: 0;
  border-radius: 0;
  word-break: break-all;
}

.text-black {
  color: #000;
}

.headerSection {
  padding: 30px 0;
  border-bottom: 3px solid #05386b;
}

.formHeader {
  font-size: 25px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 50px 0;
}

textarea {
  background: transparent;
  border: 2px solid #05386b;
  outline: none;
  padding: 5px;
  color: #05386b;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.3;
}

h1 {
  font-size: 45px;
  text-transform: uppercase;
  font-weight: 700;
  padding: 50px 0;
  letter-spacing: 5px;
}

h4 {
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 2px;
}

#compareForm {
  padding-bottom: 50px;
  font-size: 0;
}

#compareForm input[type="submit"] {
  margin: 50px 0 0 !important;
}

.btn-info {
  color: #fff;
  background-color: #05386b;
  border-color: #05386b;
}

.consolidatedReport ul {
  list-style: none;
  padding-bottom: 50px;
}

.consolidatedReport ul li {
  display: inline-block;
  vertical-align: middle;
  margin-right: 30px;
  font-size: 18px;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
}

.consolidatedReport ul li:last-child {
  margin-right: 0;
}

ul {
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.headerSection img {
  max-width: 80%;
}

